    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.cover-pic {
    object-fit: cover;
}

.contain-pic {
    object-fit: contain;
}

.footer-page {
    margin-top: auto;
}


/* Для применения line-height */

span {
    display: inline-block;
}



/*=====================================*/
/*basic styles*/
/*=====================================*/
html {
    height: 100%;
}

body {
    font-weight: 400;
    min-height: 100%;
    min-width: 320px;
    position: relative;
    font-family: "Montserrat", Helvetica, Arial, sans-serif;
    overflow-x: hidden;
    color: #444444;
    width: 100%;
    height: 100%;
    background-color: #fff;
    line-height: 1.5;
    text-rendering: optimizeLegibility;
    -webkit-text-decoration-skip: objects;
            text-decoration-skip: objects;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
    width: device-width;
}

/*=====================================*/
/*basic styles - END*/
/*=====================================*/

